import { gsap } from "gsap";

export default function Navbar(props) {
  var tl = gsap.timeline({ defaults: { duration: .6, ease: "expo.inOut" } });

  const open = () => {
    if (tl.reversed()) {
      tl.play();
    } else {
      tl.to("nav", { right: 0 })
        .to("nav", { height: "100vh" }, "-=.1")
        .to(
          "nav ul li a",
          { opacity: 1, pointerEvents: "all", stagger: 0.2 },
          "-=.8"
        )
        .to(".close", { opacity: 1, pointerEvents: "all" }, "-=.8")
        .to("nav h2", { opacity: 1 }, "-=1");
    }
    
  };
  const close = () => {
    tl.reverse();
  };

  return (
    <>
      <div className="navcontainer" onClick={open}>
        <div className="bars"></div>
      </div>
      <nav>
        <h2 className="text-sm sm:text-3xl md:text-4xl">{props.title}</h2>
        <div className="close" onClick={close}>
          <div />
        </div>
        <ul>
          <li>
            <a href="#" onClick={close} className="text-sm sm:text-3xl md:text-4xl click">Home</a>
          </li>
          <li>
            <a href="#about" onClick={close} className="text-sm sm:text-3xl md:text-4xl click">About</a>
          </li>
          <li>
            <a href="#skill" onClick={close} className="text-sm sm:text-3xl md:text-4xl click">Skill</a>
          </li>
          <li>
            <a href="#" onClick={close} className="text-sm sm:text-3xl md:text-4xl click">Projects</a>
          </li>
          <li>
            <a href="#contact" onClick={close} className="text-sm sm:text-3xl md:text-4xl click">Contact</a>
          </li>
        </ul>
      </nav>
    </>
  );
}
