import React, { useState } from "react";
import { BsFillSendCheckFill } from "react-icons/bs";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {  faInstagram } from "@fortawesome/free-brands-svg-icons";
import { faLinkedin, faTwitter } from "@fortawesome/free-brands-svg-icons";

export default function Contact(props) {
  const [msg,setmsg] = useState(null)
  props.alert(msg)
  const sub = ()=>{
    
    const scriptURL = 'https://script.google.com/macros/s/AKfycbwmFNAw_fIDvMutIEEHEMRo_r3w6_51pLtF6CQolBpvouVHOdQ3fgP4g-5_tx-Kn5UH/exec'
    const form = document.forms['submit-to-google-sheet']
    
    form.addEventListener('submit', e => {
      e.preventDefault()
      fetch(scriptURL, { method: 'POST', body: new FormData(form)})
        .then(response => {
          setmsg("Message sent successfully")
          
          setTimeout(()=>{
            setmsg(null)
          },1000)
          form.reset()
          
          return
    
        })
        .catch(error => console.log("Err"))
       
    })
      }

  return (
    <div>
    <section className="text-gray-600 body-font relative" data-aos="fade-right">
       <h2 className="text-3xl md:text-5xl text-center pt-5 md:pt-4 " id="contact">Contact</h2>
       <div className="container px-5 py-24 mx-auto">
          <div className="lg:w-1/2 md:w-2/3 mx-auto">
             <div className="flex flex-wrap -m-2">

              <form name='submit-to-google-sheet' className="flex flex-wrap -m-2">

                <div className="p-2 w-1/2">
                   <div className="relative">
                      <label htmlFor="name" className="leading-7 text-sm text-gray-600">Name</label>
                      <input type="text" id="name" name="Name"
                         className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" required/>
                   </div>
                </div>


                <div className="p-2 w-1/2">
                   <div className="relative">
                      <label htmlFor="Email" className="leading-7 text-sm text-gray-600">Email</label>
                      <input type="text" id="Email" name="Email"
                         className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" required/>
                   </div>
                </div>

               
                <div className="p-2 w-full">
                   <div className="relative">
                      <label htmlFor="message" className="leading-7 text-sm text-gray-600"> Message </label>
                      <textarea id="message" name="Message" className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 h-32 text-base outline-none text-gray-700 py-1 px-3 resize-none leading-6 transition-colors duration-200 ease-in-out" defaultValue={""} required/>
                   </div>
                </div>


                <div className="p-2 w-full">
                   <button className="inline-flex text-white bg-indigo-500 border-0 py-1 px-3 focus:outline-none hover:bg-indigo-600 rounded text-sm md:text-lg " onClick={sub}>
                      <BsFillSendCheckFill className=" mt-2" />
                      <span className="mx-2">Submit</span>
                   </button>
                </div>


                </form>

                <div className="p-2 w-full pt-8 mt-8 border-t border-gray-200 text-center">
                   <a className="inline-flex animate-pulse text-white bg-indigo-500 border-0 py-1 px-3 focus:outline-none hover:bg-indigo-600 rounded text-sm md:text-lg" href="mailto:sumittiwari2412@gmail.com" >
               
                     <p>Send Email</p>
                     </a>

               
                   <p className="leading-normal my-5 text-2xl">
                      Phone No :- +91-8920242595
                      <br />
                   </p>
                   <div className="md:text-3xl text-xl">
                      <a
                         href="https://instagram.com/shivanshu_tiwari_1?igshid=MzNlNGNkZWQ4Mg==" target="blank"
                         className="mx-3 hover:text-indigo-500 abouticon"
                         >
                         <FontAwesomeIcon icon={faInstagram} />
                      </a>
                      <a
                         href="https://www.linkedin.com/in/sumit-tiwari-b42362277/" target="blank"
                         className="mx-3 hover:text-indigo-500 abouticon"
                         >
                         <FontAwesomeIcon icon={faLinkedin} />
                      </a>
                      <a
                         href="https://twitter.com/sumit_tiwa51838"  target='blank'
                         className="mx-3 hover:text-indigo-500 abouticon"
                         >
                         <FontAwesomeIcon icon={faTwitter} />
                      </a>
                   </div>
                </div>
             </div>
          </div>
       </div>
    </section>
 </div>
  );
}
