import React from "react";
import { BsFillSendCheckFill } from "react-icons/bs";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faInstagram } from "@fortawesome/free-brands-svg-icons";
import { faLinkedin, faTwitter } from "@fortawesome/free-brands-svg-icons";
const animate = () => {
    window.scrollTo(0, 0);
  };

export default function Footer() {
  return (
    <div>
      <footer className="text-gray-600 body-font">
        <div className="container px-5 py-8 mx-auto flex items-center sm:flex-row flex-col">
          <a className="flex title-font font-medium items-center md:justify-start justify-center text-gray-900">
            <span className="ml-3 text-xl">Sumit Tiwari</span>
          </a>
          <p className="text-sm text-gray-500 sm:ml-4 sm:pl-4 sm:border-l-2 sm:border-gray-200 sm:py-2 sm:mt-0 mt-4">
             2023 Sumit Tiwari 
            <a
              href="https://twitter.com/knyttneve"
              className="text-gray-600 ml-1"
              rel="noopener noreferrer"
              target="_blank"
            ></a>
          </p>
          <span className="inline-flex sm:ml-auto sm:mt-0 mt-4 justify-center sm:justify-start">
            <a
              href="https://instagram.com/shivanshu_tiwari_1?igshid=MzNlNGNkZWQ4Mg=="
              target="blank"
              className="mx-3 hover:text-indigo-500 abouticon md:text-2xl"
            >
              <FontAwesomeIcon icon={faInstagram} />
            </a>

            <a
              href="https://www.linkedin.com/in/sumit-tiwari-b42362277/"
              target="blank"
              className="mx-3 hover:text-indigo-500 abouticon md:text-2xl"
            >
              <FontAwesomeIcon icon={faLinkedin} />
            </a>
            <a
              href="https://twitter.com/sumit_tiwa51838"
              target="blank"
              className="mx-3 hover:text-indigo-500 abouticon md:text-2xl"
            >
              <FontAwesomeIcon icon={faTwitter} />
            </a>

            <div className='up animate-pulse' id='up' onClick={animate}>
            <i className='fa fa-chevron-up'></i>
             </div>   

          </span>
        </div>
      </footer>
    </div>
  );
}
